<template>
  <div>
    <p>Cards search: {{ cards }}</p>
        <form class="searchForm" v-on:submit.prevent="submitSearch">
      <input type="text" v-model="searchQuery" placeholder="Type here" @keyup="getCards">
        </form>
    <div class="row">
      <div class="col-sm-6" v-for="card in cards" v-bind:key="card">
        <div class="card">
          <div class="card-body">
            <h5 class="card-title">{{ card.title }}</h5>
            <p class="card-text">{{ card.text }}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
// Vue.filter('htmlEscape', function(value) {
//   return value.replace(/\&amp\;/g, '&');
// });

export default {
  data () {
    return {
      cards: null,
      searchQuery: ''
    }
  },
  methods: {
    getCards () {
      const path = `http://localhost:5000/api/cards/`
      var request = path + this.searchQuery
      axios.get(request)
      .then(response => {
        this.cards = response.data.cards
      })
      .catch(error => {
        console.log(error)
      })
    }
  },
  created () {
    this.getCards()
  }
}
</script>



